%btn {
    @extend %base-font-family;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    display: inline-block;
    font-weight: 600;
    text-transform: uppercase;
    text-decoration: none;
    text-align: center;
    padding: 16px 24px;
    border-radius: 10px;
    transition: color $main-transition-time, background-color $main-transition-time, border-color $main-transition-time;
    font-size: 16px;
    line-height: 1.375;
    z-index: 1;
}

.btn-main {
    @extend %btn;

    color: #ffffff;
    overflow: hidden;

    &.btn-input {
        padding: 0;

        button,
        input[type="submit"] {
            padding: 16px 24px;
        }
    }

    &:active,
    &:focus,
    &:hover {
        color: #ffffff;

        &:before {
            opacity: 1;
        }

        &:after {
            opacity: 0;
        }
    }

    &:after {
        content: '';
        background: linear-gradient(134.27deg, #00C400 5.56%, #06EF39 93.01%);
        opacity: 1;
        z-index: -1;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: all .3s;
    }

    &:before {
        content: '';
        background: linear-gradient(134.27deg, rgba(0, 196, 0, 0.7) 5.56%, rgba(6, 239, 57, 0.7) 93.01%);
        opacity: 0;
        z-index: -1;
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        transition: all .3s;
    }

    button,
    input[type="submit"] {
        cursor: pointer;
        @extend %base-font-family;
        font-size: 16px;
        font-weight: 600;
        line-height: 1.375;
        color: #ffffff;
        background-color: transparent;
        border: none;
        text-transform: uppercase;
        padding: 0;
    }
}

.btn-icon-left {
    i {
        margin-right: 16px;
    }
}

.btn-icon-right {
    i {
        margin-left: 16px;
    }
}

.btn-icon-both {
    i:first-child {
        margin-left: 16px;
    }

    i:last-child {
        margin-right: 16px;
    }
}

.btn-green {
    @extend %btn;

    color: #00C400;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 48px;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    border: 1px solid #00C400;
    border-radius: 100px;

    font-weight: 600;
    font-size: 20px;
    line-height: 26px;


    &:hover {
        color: #66dc66;
        border-color: #99e799;
    }
}

.btn-red {
    @extend %btn;

    color: #ff0000;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 48px;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    border: 1px solid #ff0000;
    border-radius: 100px;

    font-weight: 600;
    font-size: 20px;
    line-height: 26px;


    &:hover {
        color: #ff3333;
        border-color: #ff3333;
    }
}

.btn-disabled {
    @extend %btn;

    color: #999999;
    display: inline-flex;
    flex-direction: column;
    align-items: center;
    padding: 20px 48px;
    left: 0%;
    right: 0%;
    top: 0%;
    bottom: 0%;
    border: 1px solid #999999;
    border-radius: 100px;

    font-weight: 600;
    font-size: 20px;
    line-height: 26px;

    pointer-events: none;
    cursor: default;


    &:hover {
        color: #999999;
        border-color: #999999;
    }
}

