.courses {
    margin-top: -100px;
    padding-bottom: 80px;
    position: relative;

    @include break(sm) {
        margin-top: -220px;
    }

    @include break(xs) {
        margin-top: -100px;
    }

    &:before {
        content: '';
        background-color: #E6E6E6;
        z-index: -10;
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }
}

.courses-carousel {
    display: flex;
    flex-wrap: wrap;
    width: calc(100% + 20px);
    margin-left: -10px;
    justify-content: space-between;

    .slick-track {
        display: flex;
    }
}

.course-box-wrapper {
    width: calc(50% - 20px);
    margin: 10px;
    height: auto;

    @include break(sm) {
        margin: 10px 0;
        width: 100%;
    }
}

.mobile .course-box-wrapper,
.tablet .course-box-wrapper {
    margin: 10px;

    @include break(sm) {
        width: calc(100% - 20px);
    }
}

.course-box {
    border: 1px solid #E6E6E6;
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 64px;
    height: 100%;
    width: 100%;
    border-radius: 10px;

    @include break(xs) {
        padding: 25px 16px;
    }

    &>*:last-child {
        margin-top: auto;
    }

    .upper-content {
        display: flex;
        flex-wrap: wrap;
    }   

    .upper-caption {
        font-weight: 600;
        font-size: 20px;
        line-height: 26px;
        text-transform: uppercase;
        color: #999999;
        width: 50%;

        @include break(xs) {
            width: 100%;
        }
    }

    .period {
        font-size: 16px;
        line-height: 26px;
        color: #333333;
        width: 50%;
        text-align: right;
        font-weight: 600;

        @include break(xs) {
            width: 100%;
            text-align: left;
            margin-top: 16px;
            font-size: 14px;
            line-height: 133%;
        }
    }

    .h4 {
        font-weight: 600;
        font-size: 36px;
        line-height: 52px;
        color: #333333;

        margin-top: 16px;
        padding-bottom: 24px;
        margin-bottom: 24px;
        position: relative;

        @include break(xs) {
            font-size: 20px;
            line-height: 24px;
        }

        &:before {
            position: absolute;
            content: "";
            display: block;
            left: 0;
            bottom: 0;
            width: 90px;
            height: 2px;
            background-color: #1aca1a;
            z-index: 1;
        }
    }

    .description {
        margin-bottom: 32px;
    }
}

.course-box-finished {
    background-color: #cccccc;
    color: #999999;

    .period,
    .h4,
    .description {
        color: #999999;
    }

    .h4:before {
        opacity: .5;
    }
}
